input.submit--button--component {
    background-color: rgb(62, 62, 62);
    color: white;
    width: 100%;
    height: 36px;
    margin: 1rem 0;
    border: 0;
    border-radius: 0;
    font-size: 14px;

    @media (min-width: 768px) {
        height: 44px;
        font-size: 15px;
    }

    &.order {
        display: block;
        background-color: rgb(128, 211, 142);
        max-width: 360px;
        margin: 1rem auto;
    }
}
