section#notice-detail {
    hgroup {
        margin-bottom: 2rem;

        @media (min-width: 768px) {
            margin-bottom: 3rem;
        }
        h2 {
            font-size: 18px;
            font-weight: bolder;

            @media (min-width: 768px) {
                font-size: 24px;
            }
        }
    }
    article.content--box {
        .title {
            display: flex;
            align-items: center;
            height: 44px;
            border-top: 5px solid rgba(62, 62, 62, 1);
            padding: 0 0.5rem;

            p {
                width: 340px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                font-size: 15px;

                @media (min-width: 768px) {
                    width: 100%;
                    font-size: 16px;
                }
            }
        }

        .description {
            border-top: 2px solid rgba(112, 112, 122, 1);
            border-bottom: 2px solid rgba(112, 112, 122, 1);

            .append {
                font-size: 12px;

                @media (min-width: 768px) {
                    font-size: 13px;
                }
            }

            .box {
                min-height: 500px;
                padding: 3rem 0;
                font-size: 15px;

                @media (min-width: 768px) {
                    font-size: 16px;
                }
            }
        }
    }

    .go--list-btn {
        max-height: 36px !important;
        margin: 0;
    }
}
