section#detail {
    .container {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        padding: 0 1rem;

        @media (min-width: 768px) {
            padding: 0 1.5rem;
        }
        @media (min-width: 1366px) {
            padding: 0 2rem;
        }
        @media (min-width: 1440px) {
            padding: 0;
        }

        article.head {
            display: flex;
            flex-wrap: wrap;
            padding: 4rem 0;

            @media (min-width: 768px) {
                padding: 8rem 0;
            }

            .represent--image-wrap {
                overflow: hidden;
                width: 100%;

                @media (min-width: 768px) {
                    width: 40%;
                }

                figure {
                    max-height: 420px;

                    @media (min-width: 1680px) {
                        max-height: 650px;
                    }

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }

            .sale--data-wrap {
                width: 100%;
                padding-top: 2rem;

                @media (min-width: 768px) {
                    padding-top: 0;
                    padding-left: 3rem;
                    width: 60%;
                }
                @media (min-width: 1366px) {
                    padding-left: 7rem;
                }

                hgroup {
                    h1 {
                        font-size: 22px;
                        margin-bottom: 0.7rem;

                        @media (min-width: 768px) {
                            margin-bottom: 1rem;
                            font-size: 32px;
                        }
                    }

                    span {
                        color: rgb(128, 211, 142);
                        font-size: 14px;

                        @media (min-width: 768px) {
                            font-size: 15px;
                        }
                    }
                }

                .rating {
                    display: flex;
                    margin: 1.5rem 0 2rem;

                    @media (min-width: 1024px) {
                        margin: 1.5rem 0 3rem;
                    }
                    @media (min-width: 1440px) {
                        margin: 2rem 0 4rem;
                    }

                    figure {
                        position: relative;
                        width: 100px;
                        overflow: hidden;
                        margin-right: 0.8rem;
                        margin-top: -1px;

                        @media (min-width: 768px) {
                            width: 110px;
                        }

                        img {
                            width: 100%;
                            height: 100%;
                            position: relative;
                            z-index: 2;
                        }

                        figcaption {
                            @include centeredY();
                            left: -100%;
                            width: 100%;
                            height: 88%;
                            background-color: rgb(128, 211, 142);

                            &.average--05 {
                                left: -92.5%;
                            }
                            &.average--10 {
                                left: -81%;
                            }
                            &.average--15 {
                                left: -71%;
                            }
                            &.average--20 {
                                left: -61%;
                            }
                            &.average--25 {
                                left: -49.95%;
                            }
                            &.average--30 {
                                left: -40%;
                            }
                            &.average--35 {
                                left: -29%;
                            }
                            &.average--40 {
                                left: -18%;
                            }
                            &.average--45 {
                                left: -7.5%;
                            }
                            &.average--50 {
                                left: 0;
                            }
                        }
                    }
                }

                ul {
                    li {
                        display: flex;
                        min-height: 30px;
                        font-size: 12px;

                        @media (min-width: 1024px) {
                            min-height: 34px;
                            font-size: 14px;
                            // margin-bottom: 0.1rem;
                        }
                        @media (min-width: 1440px) {
                            min-height: 40px;
                            font-size: 14px;
                            margin-bottom: 0.3rem;
                        }

                        &:first-child {
                            .box {
                                margin-top: -1px;
                                font-weight: bolder;

                                @media (min-width: 768px) {
                                    margin-top: -4px;
                                }

                                b {
                                    margin-right: 0.5rem;
                                    font-size: 15px;

                                    @media (min-width: 768px) {
                                        font-size: 20px;
                                    }
                                }
                            }
                        }

                        &:nth-child(3) {
                            .box {
                                .flex {
                                    margin-bottom: 1rem;

                                    @media (min-width: 768px) {
                                        margin-bottom: 1.7rem;
                                    }

                                    .dawn,
                                    .general {
                                        margin-right: 1rem;

                                        @media (min-width: 768px) {
                                            margin-right: 1.2rem;
                                        }

                                        & + div {
                                            p {
                                                padding-top: 0.3rem;
                                            }
                                        }
                                    }

                                    .dawn {
                                        color: rgb(128, 211, 142);
                                    }
                                }
                            }
                        }

                        // &:last-child {
                        //     .box {
                        //     }
                        // }

                        .title {
                            min-width: 80px;

                            @media (min-width: 768px) {
                                min-width: 100px;
                            }
                        }
                    }
                }

                .total--wrap {
                    position: relative;
                    height: 50px;
                    margin-bottom: 1.5rem;

                    @media (min-width: 768px) {
                        margin-bottom: 2.5rem;
                        height: 60px;
                    }

                    div {
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        font-size: 14px;

                        @media (min-width: 768px) {
                            font-size: 15px;
                        }

                        b.price {
                            font-size: 22px;
                            font-weight: bolder;
                            margin: 0 0.3rem 0 1rem;
                            color: rgb(128, 211, 142);

                            @media (min-width: 768px) {
                                margin: 0 0.3rem 0 1.3rem;
                                font-size: 35px;
                            }
                        }

                        span {
                            font-weight: bolder;
                        }
                    }
                }

                .buttons--wrap {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    @media (min-width: 1024px) {
                        flex-direction: row;
                    }

                    button,
                    input[type='submit'] {
                        height: 36px;
                        width: 100%;
                        font-size: 14px;

                        @media (min-width: 768px) {
                            height: 44px;
                            font-size: 15px;
                        }
                        @media (min-width: 1024px) {
                            max-width: 260px;
                            height: 44px;
                            font-size: 15px;
                        }
                        @media (min-width: 1366px) {
                            max-width: 320px;
                            height: 44px;
                            font-size: 15px;
                        }
                        @media (min-width: 1440px) {
                            max-width: 360px;
                            height: 44px;
                            font-size: 15px;
                        }
                    }

                    button {
                        border: 1px solid rgba(0, 0, 0, 0.5);
                        background-color: white;
                    }
                    input[type='submit'] {
                        border: 0;
                        border-radius: 0;
                        background-color: rgb(128, 211, 142);
                        color: white;
                    }
                }
            }
        }

        article.description,
        article.appraise {
            .scrolling--wrap {
                display: flex;
                align-items: flex-end;
                background-color: rgba(62, 62, 62, 1);
                height: 40px;
                margin: 0 -1rem;

                @media (min-width: 768px) {
                    margin: 0;
                    height: 50px;
                }

                a[data-scroll] {
                    width: 50%;
                    height: 37px;
                    @include flexCentered();

                    @media (min-width: 768px) {
                        width: 150px;
                        height: 47px;
                    }
                    @media (min-width: 1024px) {
                        width: 180px;
                        height: 47px;
                    }

                    &.white {
                        background-color: white;
                    }

                    &.black {
                        background-color: rgba(62, 62, 62, 1);
                        color: white;
                    }
                }
            }
        }

        article.description {
            #product-description {
                // 지워야 하는 프로퍼티
                min-height: 500px;
            }
        }

        article.appraise {
            #product-rating {
                padding: 4rem 0 3rem;

                @media (min-width: 768px) {
                    padding: 5rem 0 4rem;
                }

                .flex {
                    &:first-child {
                        input {
                            max-width: 55px;
                            text-align: center;
                            border: 0;
                            margin-left: 1rem;
                            font-size: 30px;
                            font-weight: bolder;

                            @media (min-width: 768px) {
                                margin-left: 2rem;
                                font-size: 40px;
                                max-width: 70px;
                            }
                        }
                    }
                    &:last-child {
                        padding-top: 2rem;
                        font-size: 15px;

                        @media (min-width: 768px) {
                            font-size: 18px;
                        }
                    }
                }
            }
        }
    }
}
