.count--component {
    display: flex;
    width: 90px;
    height: 28px;

    &.responsive {
        width: 40px;
        height: 15px;
        font-size: 12px;

        .result {
            padding: 0 0.3rem;
        }

        .minus,
        .plus {
            img {
                width: 13px;
            }
        }

        // @media (min-width: 768px) {
        //     width: 65px;
        //     height: 20px;
        //     font-size: 14px;

        //     .result {
        //         padding: 0;
        //     }
        //     .minus,
        //     .plus {
        //         img {
        //             width: 15px;
        //         }
        //     }
        // }

        @media (min-width: 768px) {
            width: 75px;
            height: 23px;
            font-size: 14px;

            .result {
                padding: 0;
            }
            .minus,
            .plus {
                img {
                    width: 16px;
                }
            }
        }
    }

    .minus,
    .result,
    .plus {
        flex: 1;
        @include flexCentered();
        height: 100%;
    }

    .result {
        flex: 1.5;
    }

    .minus,
    .plus {
        cursor: pointer;
        background-color: rgb(233, 233, 233);

        img {
            width: 18px;
            opacity: 0.3;
        }
    }
}
