.banner--early-container {
    overflow: hidden;
    background-color: rgba(62, 62, 62, 1);
    width: 100%;

    .wrap {
        position: relative;
        height: 300px;

        @media (min-width: 768px) {
            height: 350px;
        }

        .comments {
            position: absolute;
            top: 30%;
            left: 35%;
            transform: translate(-50%, -50%);
            color: white;

            @media (min-width: 768px) {
                top: 50%;
                left: 30%;
            }
            @media (min-width: 1920px) {
                left: 35%;
            }

            p {
                font-size: 18px;
                line-height: 1.3;

                @media (min-width: 768px) {
                    font-size: 35px;
                }

                span {
                    color: rgba(128, 211, 142, 1);
                    font-size: 26px;
                    font-weight: bolder;
                    padding-left: 1rem;

                    @media (min-width: 768px) {
                        font-size: 52px;
                    }
                }
            }

            a.detail--view-btn {
                @include flexCentered();
                width: 100px;
                height: 26px;
                font-size: 13px;
                border: 1px solid rgba(255, 255, 255, 0.5);
                margin-top: 0.8rem;

                @media (min-width: 768px) {
                    width: 160px;
                    height: 33px;
                    font-size: 17px;
                    margin-top: 1.5rem;
                }
            }
        }

        img.delivery--truck {
            position: absolute;
            right: -35%;
            bottom: -45%;
            width: 330px;

            @media (min-width: 768px) {
                width: 350px;
                right: -10%;
                bottom: -30%;
            }
            @media (min-width: 1024px) {
                right: 5%;
            }
            @media (min-width: 1366px) {
                right: 10%;
            }
            @media (min-width: 1440px) {
                right: 15%;
            }
            @media (min-width: 1920px) {
                right: 25%;
            }
            @media (min-width: 1920px) {
                right: 25%;
            }
        }
    }
}
