.banner--sale-container {
    position: relative;
    overflow: hidden;
    background-color: rgba(62, 62, 62, 1);
    width: 100%;

    .wrap {
        width: 100%;
        height: 100px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @media (min-width: 768px) {
            flex-direction: row;
            height: 125px;
        }

        p {
            &:first-child {
                color: white;
                width: 100%;
                font-size: 20px;
                margin-bottom: 0.7rem;

                @media (min-width: 768px) {
                    width: auto;
                    margin-right: 3rem;
                    margin-bottom: 0;
                    font-size: 28px;
                }
                @media (min-width: 1440px) {
                    margin-right: 5rem;
                    font-size: 36px;
                }

                span {
                    font-weight: lighter;
                }
            }

            &:last-child {
                color: rgba(128, 211, 142, 1);
                font-weight: bolder;
                font-size: 30px;
                width: 100%;

                @media (min-width: 768px) {
                    width: auto;
                    font-size: 41px;
                }
                @media (min-width: 1440px) {
                    font-size: 52px;
                }
            }
        }
    }
}
