nav.notice--navi-component {
    ul {
        li {
            font-weight: bolder;
            margin-bottom: 1.5rem;
            font-size: 18px;
            color: rgba(62, 62, 62, 0.1);

            @media (min-width: 768px) {
                font-size: 28px;
                margin-bottom: 2rem;
            }

            &:last-child {
                margin-bottom: 0;
            }

            &.active {
                color: rgba(62, 62, 62, 1);
            }
        }
    }
}
