section#join-success {
    padding-top: 100px;

    @media (min-width: 768px) {
        padding-top: 150px;
    }

    article.container {
        width: 100%;
        max-width: 1300px;
        margin: 0 auto;
        padding: 0 1rem;

        .line {
            background: rgba(62, 62, 62, 1);

            &.thin {
                height: 2px;

                @media (min-width: 768px) {
                    height: 3px;
                }
            }

            &.thick {
                height: 5px;

                @media (min-width: 768px) {
                    height: 7px;
                }
            }
        }

        .comment--wrap {
            text-align: center;

            h2 {
                font-size: 18px;
                font-weight: bolder;
                margin-bottom: 3rem;

                @media (min-width: 768px) {
                    font-size: 22px;
                }

                @media (min-width: 1366px) {
                    font-size: 25px;
                }
            }

            p {
                margin-bottom: 1rem;
                font-size: 13px;

                @media (min-width: 768px) {
                    font-size: 14px;
                }

                @media (min-width: 1366px) {
                    font-size: 15px;
                }
            }
        }

        .success--login-btn {
            background-color: white;
            color: rgba(128, 211, 142, 1);
            border: 1px solid gray;
            max-width: 350px !important;
            margin-left: 0 !important;
            margin-top: 2rem;
            margin-bottom: 3rem;

            @media (min-width: 768px) {
                margin-top: 3rem;
                margin-bottom: 4rem;
            }
        }

        .event-title {
            margin: 1rem 0;
            font-size: 15px;
            font-weight: bold;

            @media (min-width: 768px) {
                font-size: 16px;
            }
        }

        .sale--banner {
            margin-top: 1rem;

            @media (min-width: 768px) {
                margin-top: 2rem;
            }

            .wrap {
                p {
                    padding-left: 1rem;

                    @media (min-width: 768px) {
                        padding-left: 0;
                    }
                }
            }
        }

        .early--banner {
            margin-top: 1rem;

            @media (min-width: 768px) {
                margin-top: 2rem;
            }

            .wrap {
                height: 250px;

                @media (min-width: 768px) {
                    height: 300px;
                }

                .delivery--truck {
                    @media (max-width: 767px) {
                        bottom: -60%;
                    }
                    @media (min-width: 768px) {
                        bottom: -45%;
                    }
                    @media (min-width: 1920px) {
                        bottom: -43%;
                        right: 15%;
                    }
                }
            }
        }

        .catering--banner {
            position: relative;
            overflow: hidden;
            border: 1px solid rgba(62, 62, 62, 0.3);
            width: 100%;
            margin-top: 1rem;

            @media (min-width: 768px) {
                margin-top: 2rem;
            }

            .wrap {
                width: 100%;
                height: 100px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                @media (min-width: 768px) {
                    flex-direction: row;
                    height: 125px;
                }

                p {
                    padding-left: 1rem;

                    @media (min-width: 768px) {
                        padding-left: 0;
                    }

                    &:first-child {
                        color: white;
                        width: 100%;
                        font-size: 20px;
                        margin-bottom: 0.7rem;

                        @media (min-width: 768px) {
                            width: auto;
                            margin-bottom: 0;
                            font-size: 28px;
                            order: 2;
                        }
                        @media (min-width: 1440px) {
                            font-size: 36px;
                        }

                        span {
                            font-weight: lighter;
                        }
                    }

                    &:last-child {
                        color: rgba(128, 211, 142, 1);
                        font-weight: bolder;
                        font-size: 30px;
                        width: 100%;

                        @media (min-width: 768px) {
                            width: auto;
                            font-size: 41px;
                            margin-right: 3rem;
                            order: 1;
                        }
                        @media (min-width: 1440px) {
                            margin-right: 5rem;
                            font-size: 52px;
                        }
                    }
                }
            }
        }
    }
}
