article.board--container-component {
    width: 100%;
    border-top: 2px solid rgb(112, 112, 112);
    border-bottom: 2px solid rgba(112, 112, 112, 1);

    ul.head,
    nav.body {
        a.board--row,
        li.board--row {
            display: flex;
            align-items: center;
            font-size: 12px;

            @media (min-width: 768px) {
                font-size: 15px;
            }

            .board--col {
                flex: 1;

                &:first-child,
                &:nth-child(3),
                &:last-child {
                    display: flex;
                    justify-content: center;
                }

                &:first-child {
                    max-width: 30px;

                    @media (min-width: 768px) {
                        max-width: 50px;
                    }
                }

                &:nth-child(2) {
                    flex: 2.5;
                    display: flex;
                    align-items: center;

                    @media (min-width: 768px) {
                        flex: 3.5;
                    }
                }

                &:last-child {
                    max-width: 30px;

                    @media (min-width: 768px) {
                        max-width: 50px;
                    }
                }
            }
        }
    }

    ul.head {
        a.board--row,
        li.board--row {
            min-height: 50px;
            font-weight: bolder;
            border-bottom: 1px solid rgba(62, 62, 62, 0.6);

            .board--col {
                &:nth-child(2) {
                    justify-content: center;
                }
            }
        }
    }

    nav.body {
        a.board--row,
        li.board--row {
            min-height: 60px;
            padding: 0.5rem 0;
            border-bottom: 1px solid rgba(62, 62, 62, 0.1);

            &:last-child {
                border: 0;
            }

            &.empty {
                .board--col {
                    &:first-child {
                        max-width: initial;
                        font-weight: bolder;
                        padding: 5rem 0;

                        @media (min-width: 768px) {
                            font-size: 14px;
                            padding: 7rem 0;
                        }

                        figure {
                            width: 100%;
                            @include flexCentered();
                            cursor: initial;

                            img {
                                width: 16px;
                                height: 16px;
                                margin-bottom: -2px;
                                margin-right: 5px;
                                opacity: 0.5;
                            }
                        }
                    }
                }
            }

            .board--col {
                &:nth-child(2) {
                    figure {
                        max-width: 40px;
                        max-height: 60px;
                        overflow: hidden;
                        margin-left: 0.5rem;

                        @media (min-width: 768px) {
                            max-width: 50px;
                            max-height: 70px;
                            margin-left: 3rem;
                        }

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                    p {
                        width: 190px;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        margin-left: 0.5rem;

                        @media (min-width: 768px) {
                            width: initial;
                            margin-left: 1rem;
                        }
                    }
                }

                &:last-child {
                    span.price {
                        margin-left: -5px;

                        @media (min-width: 768px) {
                            margin-left: 0;
                        }
                    }

                    figure {
                        position: absolute;
                        top: 50%;
                        right: 0;
                        transform: translateY(-50%);
                        width: 16px;
                        height: 16px;
                        cursor: pointer;

                        @media (min-width: 768px) {
                            width: 18px;
                            height: 18px;
                        }

                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
            }
        }
    }
}
