section#basket,
section#order,
section#order-complete {
    .container {
        padding: 5rem 1rem;
        width: 100%;
        max-width: 1300px;
        margin: 0 auto;

        table.basket--table {
            width: 100%;
            border-top: 5px solid rgb(62, 62, 62);
            border-bottom: 1px solid rgba(62, 62, 62, 0.6);
            margin: 4rem 0 0;

            @media (min-width: 768px) {
                margin: 5rem 0 0;
            }

            thead,
            tbody {
                tr {
                    display: flex;
                    align-items: center;
                    font-size: 12px;

                    @media (min-width: 768px) {
                        font-size: 15px;
                    }

                    th,
                    td {
                        flex: 1;

                        &:first-child,
                        &:nth-child(3),
                        &:last-child {
                            display: flex;
                            justify-content: center;
                        }

                        &:first-child {
                            max-width: 20px;

                            @media (min-width: 768px) {
                                max-width: 50px;
                            }
                        }

                        &:nth-child(2) {
                            flex: 2.5;
                            display: flex;
                            align-items: center;

                            @media (min-width: 768px) {
                                flex: 3.5;
                            }
                        }
                    }
                }
            }

            thead {
                tr {
                    min-height: 50px;
                    font-weight: bolder;
                    border-bottom: 1px solid rgba(62, 62, 62, 0.6);

                    th {
                        &:nth-child(2) {
                            justify-content: center;
                        }
                    }
                }
            }

            tbody {
                tr {
                    min-height: 60px;
                    padding: 0.5rem 0;
                    border-bottom: 1px solid rgba(62, 62, 62, 0.1);

                    &:last-child {
                        border: 0;
                    }

                    &.empty {
                        td {
                            &:first-child {
                                max-width: initial;
                                font-weight: bolder;
                                padding: 5rem 0;

                                @media (min-width: 768px) {
                                    font-size: 14px;
                                    padding: 7rem 0;
                                }

                                figure {
                                    width: 100%;
                                    @include flexCentered();
                                    cursor: initial;

                                    img {
                                        width: 16px;
                                        height: 16px;
                                        margin-bottom: -2px;
                                        margin-right: 5px;
                                        opacity: 0.5;
                                    }
                                }
                            }
                        }
                    }

                    td {
                        position: relative;

                        &:nth-child(2) {
                            figure {
                                max-width: 40px;
                                max-height: 60px;
                                overflow: hidden;
                                margin-left: 0.5rem;

                                @media (min-width: 768px) {
                                    max-width: 50px;
                                    max-height: 70px;
                                    margin-left: 3rem;
                                }

                                img {
                                    width: 100%;
                                    height: 100%;
                                    object-fit: cover;
                                }
                            }
                            p {
                                width: 140px;
                                white-space: nowrap;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                margin-left: 0.5rem;

                                @media (min-width: 768px) {
                                    width: initial;
                                    margin-left: 1rem;
                                }
                            }
                        }

                        &:last-child {
                            span.price {
                                margin-left: -5px;

                                @media (min-width: 768px) {
                                    margin-left: 0;
                                }
                            }

                            figure {
                                position: absolute;
                                top: 50%;
                                right: 0;
                                transform: translateY(-50%);
                                width: 16px;
                                height: 16px;
                                cursor: pointer;

                                @media (min-width: 768px) {
                                    width: 18px;
                                    height: 18px;
                                }

                                img {
                                    width: 100%;
                                    height: 100%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
