ul.calculated--total-component {
    border-top: 5px solid rgb(62, 62, 62);
    border-bottom: 5px solid rgb(62, 62, 62);
    text-align: right;
    font-size: 14px;
    padding: 2rem 0;
    margin: 4rem 0 3rem;

    @media (min-width: 768px) {
        font-size: 15px;
        margin: 8rem 0 4rem;
    }

    li {
        position: relative;
        margin-bottom: 2rem;

        &:last-child {
            .box {
                display: flex;
                align-items: center;

                p {
                    &:last-child {
                        color: rgb(128, 211, 142);
                        font-size: 20px;
                    }
                }
            }
        }

        .arithmetic {
            left: 37%;
            @include centeredY();
            width: 17px;
            height: 17px;
            background-color: rgba(62, 62, 62, 1);

            &.white {
                background-color: white;
            }

            &.minus {
                &:before {
                    content: '';
                    @include centered();
                    width: 70%;
                    height: 1px;
                    background-color: white;
                }
            }

            &.plus {
                &:before {
                    content: '';
                    @include centered();
                    width: 70%;
                    height: 1px;
                    background-color: white;
                }

                &:after {
                    content: '';
                    @include centered();
                    width: 1px;
                    height: 70%;
                    background-color: white;
                }
            }

            &.equal {
                &:before {
                    content: '';
                    @include centered();
                    top: 30%;
                    width: 70%;
                    height: 1px;
                    background-color: rgb(62, 62, 62);
                }

                &:after {
                    content: '';
                    @include centered();
                    top: 75%;
                    width: 70%;
                    height: 1px;
                    background-color: rgb(62, 62, 62);
                }
            }
        }

        .box {
            display: flex;
            justify-content: flex-end;

            p {
                &:last-child {
                    min-width: 130px;
                    span {
                        padding: 0 0.3rem;
                    }
                }
            }
        }
    }

    @media (min-width: 768px) {
        display: flex;

        li {
            flex: 1;
            margin: 3rem 0 !important;

            .arithmetic {
                left: 0;
                transform: translate(-50%, -50%);
                width: 25px;
                height: 25px;

                &.minus {
                    &:before {
                        width: 70%;
                        height: 2px;
                    }
                }
                &.plus {
                    &:before {
                        width: 70%;
                        height: 2px;
                    }

                    &:after {
                        width: 2px;
                        height: 70%;
                    }
                }
                &.equal {
                    &:before {
                        top: 30%;
                        height: 2px;
                    }

                    &:after {
                        top: 75%;
                        height: 2px;
                    }
                }
            }

            .box {
                flex-direction: column;
                align-items: center;

                p {
                    &:last-child {
                        min-width: initial;
                        padding-top: 2rem;
                    }
                }
            }
        }
    }
}
