ul.pagination {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 350px;
    margin: 1.25rem auto 0;
    padding: 0.3rem 0.3rem;
    border: 1px solid rgba(62, 62, 62, 0.3);
    font-size: 13px;

    @media (min-width: 768px) {
        font-size: 14px;
        margin: 2rem auto 0;
        padding: 0.5rem 0.3rem;
    }

    li {
        &.counts {
            color: rgba(62, 62, 62, 0.4);

            &.active {
                color: rgba(62, 62, 62, 1);
            }
        }

        &.first,
        &.last {
            margin-bottom: -3px;
        }

        &.first {
            margin-right: -15px;
        }

        &.last {
            margin-left: -15px;
        }
    }
}
