section#order,
section#order-complete {
    .container {
        .prev--step {
            max-width: 100px;
            max-height: 36px;
            margin: 4rem 0 1rem;

            @media (min-width: 768px) {
                margin: 5rem 0 1rem;
            }

            &:before {
                content: '';
                display: inline-block;
                width: 8px;
                height: 8px;
                border: 1px solid rgba(62, 62, 62, 1);
                border-top: 1px solid rgba(62, 62, 62, 0);
                border-right: 1px solid rgba(62, 62, 62, 0);
                margin-right: 2px;
                transform: rotateZ(45deg);
            }
        }

        table.basket--table {
            margin: 0;
        }

        ul.order--board {
            margin: 4rem 0 1rem;

            @media (min-width: 768px) {
                margin: 5rem 0 1rem;
            }
            li {
                min-height: 60px;
                display: flex;
                border-bottom: 1px solid rgb(112, 112, 112);
                font-size: 12px;

                @media (min-width: 768px) {
                    font-size: 14px;
                }

                &:first-child {
                    min-height: 40px;
                    border-bottom: 5px solid rgb(62, 62, 62);
                    font-weight: bolder;
                    font-size: 14px;

                    @media (min-width: 768px) {
                        font-size: 15px;
                    }
                }

                .title {
                    width: 100%;
                    max-width: 90px;
                    display: flex;
                    align-items: center;
                    background-color: rgb(244, 244, 244);
                    justify-content: center;

                    @media (min-width: 768px) {
                        max-width: 277px;
                        justify-content: flex-start;
                        padding-left: 2rem;
                    }
                }

                .description {
                    flex: 1;
                    display: flex;
                    align-items: center;
                    padding-left: 1rem;

                    @media (min-width: 768px) {
                        padding-left: 2rem;
                    }
                }

                .box {
                    width: 100%;
                    height: 250px;
                    overflow-y: scroll;
                    border: 1px solid rgba(62, 62, 62, 0.5);

                    @media (min-width: 768px) {
                        height: 280px;
                    }
                }
            }
        }
    }
}
