section#search-result {
    article.result--comment {
        padding: 3rem 0;
        @media (min-width: 768px) {
            padding: 4rem 0;
        }
        @media (min-width: 1440px) {
            padding: 5rem 0;
        }
        @media (min-width: 1680px) {
            padding: 6rem 0;
        }
        hgroup {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;

            h4 {
                padding: 0 0.3rem;
                font-weight: bolder;
                font-size: 16px;

                @media (min-width: 768px) {
                    font-size: 20px;
                }
            }
            span {
                margin-left: 0.3rem;
                font-size: 15px;

                @media (min-width: 768px) {
                    font-size: 18px;
                }
            }

            p {
                width: 100%;
                text-align: center;
                font-size: 13px;
                color: rgba(62, 62, 62, 0.5);
                margin-top: 0.8rem;

                @media (min-width: 768px) {
                    font-size: 14px;
                    margin-top: 1rem;
                }

                b {
                    font-weight: bolder;
                    color: rgba(62, 62, 62, 1);
                }
            }
        }
    }

    article.products--wrap {
        ul.filter {
            width: 100%;
            padding-bottom: 0.5rem;
            margin: 0 0 1rem 1rem;
            display: flex;
            font-size: 13px;

            @media (min-width: 768px) {
                margin: 0 0 1rem 1rem;
                font-size: 14px;
            }
            @media (min-width: 1366px) {
                margin: 0 0 1rem 4.5rem;
            }
            @media (min-width: 1680px) {
                margin: 0 0 1rem 4.2rem;
            }

            li {
                margin-right: 1rem;
                color: rgba(62, 62, 62, 0.3);

                &.selected {
                    color: rgba(62, 62, 62, 1);
                }
            }
        }
    }
}
