section#category,
section#search-result {
    .container {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        padding: 0 1rem;
        padding-top: 85px;

        @media (min-width: 768px) {
            padding: 0 2rem;
            padding-top: 120px;
        }

        article.sort--wrap {
            width: 100%;
            max-width: 869px;
            margin: 0 auto;

            hgroup.title {
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 0.3rem;

                figure {
                    display: flex;
                    align-items: center;

                    img {
                        width: 33px;

                        @media (min-width: 768px) {
                            width: 43px;
                        }
                        @media (min-width: 1366px) {
                            width: 53px;
                        }
                    }

                    figcaption {
                        margin-left: 0.4rem;
                        font-weight: bold;
                        font-size: 18px;

                        @media (min-width: 768px) {
                            margin-left: 0.6rem;
                            font-size: 20px;
                        }
                    }
                }

                select {
                    border: 0;
                    font-size: 13px;
                    height: 33px;

                    @media (min-width: 768px) {
                        display: none;
                    }
                }

                ul {
                    display: none;

                    @media (min-width: 768px) {
                        display: flex;
                        font-size: 14px;

                        li {
                            margin-right: 1rem;
                            color: rgba(62, 62, 62, 0.3);

                            &.selected {
                                color: rgba(62, 62, 62, 1);
                            }

                            &:last-child {
                                margin: 0;
                            }
                        }
                    }
                }
            }

            form {
                label {
                    display: block;
                    position: relative;
                    height: 48px;

                    @media (min-width: 768px) {
                        height: 58px;
                    }

                    input[type='text'] {
                        width: 100%;
                        height: 100%;
                        border: 1px solid rgb(112, 112, 112);
                        padding: 0 2.5rem 0 0.7rem;

                        @media (min-width: 768px) {
                            padding: 0 3.3rem 0 0.7rem;
                        }
                    }

                    input[type='submit'] {
                        position: absolute;
                        @include centeredY();
                        right: 0.8rem;
                        width: 20px;
                        height: 20px;
                        border: 0;
                        background-color: white;
                        background-image: url('/assets/images/icons/search.svg');
                        background-size: 100%;
                        background-position: center;
                        background-repeat: no-repeat;

                        @media (min-width: 768px) {
                            right: 1rem;
                            width: 26px;
                            height: 26px;
                            background-size: 100%;
                        }
                    }
                }
            }

            ul.bundle {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                padding: 2rem 0;

                li {
                    display: inline-block;
                    padding: 0.5rem;
                    margin: 0 0.4rem 0.8rem 0.4rem;
                    border-radius: 10px;
                    background-color: rgb(242, 242, 242);

                    &.selected {
                        background-color: rgb(62, 62, 62);
                        color: white;
                    }

                    a {
                        span {
                            padding-left: 0.25rem;
                        }
                    }
                }
            }
        }

        article.products--wrap {
            margin: 0 -1rem;
            padding-top: 3rem;
            padding-bottom: 4rem;

            @media (min-width: 768px) {
                padding-top: 4rem;
                padding-bottom: 5rem;
            }
            @media (min-width: 1366px) {
                padding-top: 5rem;
                padding-bottom: 6rem;
            }
            @media (min-width: 1680px) {
                padding-top: 6rem;
                padding-bottom: 7rem;
            }

            ul.products--list {
                display: flex;
                flex-wrap: wrap;
                padding: 0 0.5rem;

                li {
                    width: 50%;
                    padding: 0 0.5rem;
                    display: flex;
                    justify-content: center;

                    @media (min-width: 768px) {
                        width: 33%;
                        margin-bottom: 2rem;
                    }
                    @media (min-width: 1366px) {
                        width: 33%;
                        margin-bottom: 3rem;
                    }

                    .itemcard--wrap {
                        img {
                            max-height: 230px;

                            @media (min-width: 768px) {
                                max-height: 300px;
                            }
                            @media (min-width: 1024px) {
                                max-height: 400px;
                            }
                            @media (min-width: 1024px) {
                                max-height: 420px;
                            }
                        }
                    }
                }
            }
        }
    }
}
