.itemcard--wrap {
    position: relative;
    width: 100%;
    margin-bottom: 3rem;
    max-width: 257px;

    @media (min-width: 768px) {
        max-width: 340px;
    }
    @media (min-width: 1024px) {
        max-width: 310px;
    }
    @media (min-width: 1366px) {
        max-width: 310px;
    }
    @media (min-width: 1440px) {
        max-width: 325px;
    }
    @media (min-width: 1680px) {
        max-width: 340px;
    }

    figure {
        display: flex;
        flex-direction: column;
        overflow: hidden;
        width: 100%;
        // max-width: 257px;
        margin: 0 auto;

        // @media (min-width: 768px) {
        //     max-width: 340px;
        // }
        // @media (min-width: 1366px) {
        //     max-width: 310px;
        // }
        // @media (min-width: 1440px) {
        //     max-width: 340px;
        // }

        img {
            object-fit: cover;
            max-height: 333px;

            @media (min-width: 768px) {
                max-height: 440px;
            }
            @media (min-width: 1366px) {
                max-height: 420px;
            }
            @media (min-width: 1440px) {
                max-height: 440px;
            }
        }

        figcaption {
            padding-top: 0.5rem;
            font-size: 15px;
            line-height: 1.8;

            p.price {
                font-weight: bold;
            }

            @media (min-width: 768px) {
                font-size: 18px;

                p.price {
                    font-size: 16px;
                }
            }
        }
    }

    .hit,
    .new,
    .soldout {
        position: absolute;
        top: 0;
        left: 0;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 3px;
        display: none;
        color: white;
        width: 50px;
        height: 50px;

        &.active {
            display: flex;
        }

        @media (min-width: 768px) {
            font-size: 15px;
            width: 55px;
            height: 55px;
        }
        @media (min-width: 1024px) {
            width: 60px;
            height: 60px;
        }
        @media (min-width: 1366px) {
            font-size: 16px;
            width: 65px;
            height: 65px;
        }
        @media (min-width: 1440px) {
            width: 70px;
            height: 70px;
        }
        @media (min-width: 1680px) {
            width: 75px;
            height: 75px;
        }
    }

    .hit {
        background-color: rgb(204, 50, 66);
    }
    .new {
        background-color: rgb(149, 188, 86);
    }
    .soldout {
        background-color: rgb(242, 170, 75);

        span {
            margin-top: 3px;
        }
    }
}
