button.button--type-component {
    height: 36px;
    width: 100%;
    font-size: 14px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    background-color: white;

    @media (min-width: 768px) {
        height: 44px;
        font-size: 15px;
    }
    @media (min-width: 1024px) {
        max-width: 260px;
        height: 44px;
        font-size: 15px;
    }
    @media (min-width: 1366px) {
        max-width: 320px;
        height: 44px;
        font-size: 15px;
    }
    @media (min-width: 1440px) {
        max-width: 360px;
        height: 44px;
        font-size: 15px;
    }
}
