section#order-complete {
    .buttons--container {
        border-top: 1px solid rgba(62, 62, 62, 0.5);
        margin-top: 3rem;
        padding-top: 3rem;

        @media (min-width: 768px) {
            margin-top: 5rem;
            padding-top: 5rem;
        }

        .wrap {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            max-width: 750px;
            margin: 0 auto;

            @media (min-width: 768px) {
                flex-direction: row;
            }

            button {
                margin-bottom: 1rem;

                @media (min-width: 768px) {
                    margin-bottom: 0;
                }
            }
        }
    }
}
