ul.basket--step-component {
    display: flex;
    font-size: 13px;
    margin-top: 4rem;

    @media (min-width: 768px) {
        font-size: 15px;
        margin-top: 6rem;
    }
    @media (min-width: 1366px) {
        font-size: 15px;
        margin-top: 7rem;
    }

    li {
        flex: 1;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
        padding-bottom: 0.8rem;
        color: rgba(62, 62, 62, 0.7);

        &.step {
            border-bottom: 1.5px solid rgba(0, 0, 0, 1);
        }

        &.active {
            color: rgba(62, 62, 62, 1);
            font-weight: bolder;
        }

        &:nth-child(2) {
            text-align: center;
        }

        &:last-child {
            text-align: right;
        }

        @media (min-width: 768px) {
            padding-bottom: 1.2rem;
            text-align: center;

            &:last-child {
                text-align: center;
            }
        }
    }
}
